.card {
    width: 18rem;
    border-radius: 40px !important;
    margin-bottom: 15px;
    .card-body {
        .card-title {
            width: fit-content;
            margin-left: auto;
            margin-right: auto;
            display: block; }
        .cart {
            img {
                max-width: 25px; } } }
    .img-container {
        width: max-content;
        margin-left: auto;
        margin-right: auto;
        //padding: 1rem
        //background-color: #ff0000
        //min-width: max-content
        //max-width: max-content
        //border-radius: 50%
        .card-img-top {
            min-width: 15rem;
            max-width: 15rem;
            border-width: 10px;
            background-color: #000000;
            border-radius: 40px;
            transition: transform .2s; } } }
